export default {
	data() {
		return {
			orderSn: 0,
			orderDetailInfo: {}
		}
	},
	onLoad: function(options) {
		this.orderSn = options.orderSn;
	},
	onShow() {
		this.getDetailInfo()
	},
	methods: {
		copy(res){
			var  _this=this
			uni.setClipboardData({
			    data: res,
			    success: function () {
			        _this.$common.toast('复制成功')
				
			    }
			});
		},
		call(res){
			uni.makePhoneCall({
				phoneNumber:'18188902961',
				success(res) { 
					console.log(res)
				}
			})
		},
		// 获取提货点未领取订单详情
		getDetailInfo() {
			var _this = this;
			var data = {
				order_sn: _this.orderSn
			}
			_this.$http.getOrderDetail(data).then(res => {
				let resData = res.data
				if (resData.success == 1) {
					_this.orderDetailInfo = resData.result
				} else {
					uni.showToast({
						title: resData.msg,
						icon: 'none',
						duration: 2000
					});
					return
				}
			})
		},
		//点击送回仓库
		onBack() {
			var _this = this;
			uni.showModal({
				title: '',
				content: '是否商品已清点完成，即将带回仓库',
				confirmColor: '#eb3c39',
				cancelText: '取消',
				confirmText: '确定',
				cancelColor: '#030303',
				success: function(res) {
					console.log(res);
					if (res.confirm) {
						var data = {
							order_sn: _this.orderSn
						}
						_this.$http.getWarehouse(data).then(res => {
							let resData = res.data
							if (resData.success == 1) {
								_this.getDetailInfo();
							} else {
								uni.showToast({
									title: resData.msg,
									icon: 'none',
									duration: 2000
								});
								return
							}
						})
					}
				}
			});
		}
	}
}
